.header {
  width: 100%;
  background: #4d97b3;
  margin: 0 auto;
  top: 0;
  display: flex;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;

  z-index: 999;
}
.navResponsive {
  display: none;
  justify-content: center;
  align-items: center;
  position: relative;
}
.search {
  display: flex;
  flex-direction: row;

  align-items: center;
  width: 85%;
  position: absolute;
  left: 0;
}
.searchIcon {
  text-decoration: none;
  color: black;
  background: rgba(255, 255, 255, 0.664);
  height: 100%;
  outline: none;
  border-radius: 50%;
  margin-right: 0.5rem;
  padding: 0.5rem;
}
.home-search {
  width: 80%;
  border-radius: 20px;
}
.hamburger {
  display: flex;
  justify-content: center;
  z-index: 999;
  align-items: center;
  width: 15%;
  position: absolute;
  right: 0;
  height: auto;
  padding: 0;
  margin: 0;
}
.hamburger:focus {
  outline: 0px;
}
.hamburger-inner,
.hamburger-inner:before,
.hamburger-inner:after {
  background-color: rgba(255, 255, 255, 0.664);
}

.menu {
  display: flex;
  flex-direction: row;
  width: 60%;
  align-items: center;
  text-align: center;
}
.menu a {
  text-decoration: none;
  width: 25%;
  font-size: 18px;
  font-family: sans-serif;
  color: white;
  transition: all 0.3 ease;
  padding: 0.5rem;
  position: relative;
}
.menu a:after {
  content: "";
  display: block;
  width: 0;
  height: 3px;
  background: white;
  position: absolute;
  left: 50%;
  bottom: 0;
  transition: all 0.3s;
}
.menu a:hover:after {
  left: 37.5%;
  width: 25%;
}
.menu a:hover {
  font-weight: bold;
  color: white;
  text-decoration: none;
}
.botones {
  width: 25%;

  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.imagenLogo {
  flex:0.2 1;
  object-fit: contain;
  height: 100px;
}
.login {
  border: 1.5px solid black;
  padding: 0.5rem 1rem;
  font-size: 15px;
  text-decoration: none;
  background: none;
  color: black;
  transition: color 0.4s;
  position: relative;
  overflow: hidden;
}
.login:hover {
  text-decoration: none;
  color: white;
}
.login::before {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: black;
  transform: translateX(-100%);
  transition: transform 0.4s;
  z-index: -1;
}
.login:hover::before {
  transform: translateX(0);
}
@media screen and (max-width: 960px) {
  .header {
    display: grid;
    grid-template-columns: 15% 85%;
    grid-template-rows: 2rem auto;
    grid-template-areas: "logo nav" "logo botones";
  }
  .logo {
    grid-area: logo;
    width: 100%;
  }
  .imagenLogo {
