}

.sidebar__categories:hover{
    font-weight: 900;
    color:#17a2b8;
}

.productCard{
    width:250px;
    position:relative;
    height:280px;
    display:flex;
    flex-direction: column;
    justify-content: center;
    border-radius:1rem;
    box-shadow: inset 2px 2px 5px rgba(0,0,0,.2),
                inset -5px -5px 15px rgba(255,255,255,.1),
                2px 2px 15px rgba(0,0,0,.3),
                -5px -5px 15px rgba(255,255,255, .1);
            
    overflow: hidden;
}
.productCard:hover .productCard__BuyOptions{
    left:0;
}


.productCard > img
{
    object-fit: contain;
    height:120px;
}

.productCard__container
{
    position:relative;
    top:10px;
    padding:2px;
    display:flex;
    margin:0 5px;
    flex-direction: column;
}

.productCard__container p:nth-of-type(1){
    font-size:0.9rem;
}

.productCard__container p:nth-of-type(2){
    font-size:0.7rem;
}

.productCard__BuyOptions{
position:absolute;
left:100%;
width:100%;
height:100%;
background-color:rgba(0,0,0,.8);
display:flex;
flex-direction:column;
align-items: center;
justify-content: center;
transition:0.3s ease-in;
}

.productCard__BuyOptions > button
{
    padding:0.4rem;
    font-size:1rem;
    border-radius:2rem;
    margin-bottom:30px;
    color:#17a2b8;
    background-color:transparent;
    border:2px solid #17a2b8; 
    outline:none !important;
    z-index:1;
}

.btn-seeDetails
{
color:white;
background-color:transparent;
border:2px solid white;
padding:5px;
border-radius:2rem;
}
.btn-seeDetails:hover{
    text-decoration: none;
    color:white;
}

.btn-addToBasket:hover,
.btn-seeDetails:hover
{
    font-weight: bolder;
} 

.headerProductos {
  display: grid;
  position: -webkit-sticky;
  position: sticky;
  z-index: 999;
  grid-template-columns: 10% 60% 30%;
  background: #4d97b3;
  grid-template-rows: auto;
  grid-template-areas: "logoP buscar botonesP";
  width: 100%;
}

.logo_Productos,
.buscar,
.botonesP {
  display: flex;
  align-items: center;
}
.logo_Productos {
  grid-area: logoP;
  width: 100%;
  justify-content: center;
}
.imgLogo {
  width: 90%;
  height: auto;
}
.buscar {
  grid-area: buscar;
  flex-direction: row;
  width: 100%;
}
.barraBuscar {
  width: 80%;
  height: 35%;
  border-radius: 30px;
  outline: none;
  border: none;
  padding-left: 5%;
}
.botonesP {
  grid-area: botonesP;
  flex-direction: row;
  justify-content: space-evenly;
  width: 100%;
}

.carrito {
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50%;
  width: 30%;
  color: rgba(99, 55, 55, 0.74);
  transition: color 0.4 ease;
}
.carrito:hover {
  text-decoration: none;
  color: white;
}

.header__session {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60%;
  padding: 0 0.5rem;
  text-decoration: none !important;
  outline:none;
}

.header__options
{
  display:flex;
  flex-direction: column;
  width:70%;
  padding:3px;
  border:1px solid black;
  border-radius:5px;
  background-color: black;
}

.header__options:hover{
  font-weight: 800;
}

.header__options__one
{
  font-size:0.7rem;
  font-weight: 700;
  color:#03a9f4;
}
.header__options__two
{
  font-size:0.9rem;
  text-align: center;
  color:#f3f3f3;
}




.basketItem
{
    position: relative;
    margin-top:30px;
    margin-bottom:15px;
    display:flex;
    padding:30px;
    background-color:#f3f3f3;
}

.basketItem > img{
    width:150px;
    height: 150px;
    object-fit: contain;
}

.basketItem__Info 
{
    position:relative;
    left:20px;
    display:flex;
    flex-direction: column;
    padding:10px;
}
.subtotal{
    position:relative;
    margin-top:20px;
    margin-right:20px;
    display:flex;
    flex-direction: column;
    width:250px;
    height: 160px;
    background-color: rgba(0,0,0,.3);
    border-radius:5px;
    padding:10px;
}

.subtotal > p{
    font-size:0.8rem;
}

.subtotal .subtotal__info
{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.subtotal__info >p
{
    font-size:0.8rem;
    color:black;
}
.subtotal__info  span
{
    font-size:1rem;
}

.subtotal__info button{
    color: white;
    border-radius:10px;
    background-color:#0f3959;
    outline:none !important;
    cursor:pointer;
}

.subtotal__info button:hover
{
    letter-spacing: 3px;
    background-color: black;
}


.checkBasket{
    min-height:100vh;
    width:100%;
    background-color:white;
    display:flex;
    justify-content: space-between;
}
.checkBasket__empty
{
    padding:10px;
    position:absolute;
    top:50%;
    left:50%;
    transform: translate(-50%, -50%);
}

.checkBasket__empty > h2
{
    font-size:2.5rem;
}
.checkBastet__products
{
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
}

.checkBastet__products > h3
{
    padding-left:30px;
}





